import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { MenuController, ModalController, Platform } from '@ionic/angular';


import { MessageService } from 'primeng/api';

import { InimigoDTO } from 'src/app/models/inimigo.dto';
import { NpcDTO } from '../models/Npc/npc.dto';
import { PaginacaoService } from '../services/utils/paginacao.service';
import { ItemService } from '../services/criacoes/item/item.service';
import { NpcService } from '../services/npc/npc.service';
import { UpdateNpcComponent } from '../components/criacoes/update-npc/update-npc.component';
import { FundoService } from '../services/utils/fundo.service';

@Component({
  selector: 'app-gerinimigos',
  templateUrl: './gerinimigos.page.html',
  styleUrls: ['./gerinimigos.page.scss'],
})
export class GerinimigosPage implements OnInit {

  img = '';

  npcs: NpcDTO[] = []
  display: boolean = false;

  tipoNpc: number = -1;
  listDisplay: boolean = false

  page;

  paginaAtual = 0;

  doRefresh: Subject<boolean> = new Subject<boolean>();

  requestRefresh(evt) {
    console.log('Entrou no request')
    this.doRefresh.next(true);
  }

  constructor(private router: Router, private menu: MenuController,
    private paginaService: PaginacaoService,
    private itemService: ItemService,
    private npcService: NpcService,
    private toast: MessageService,
    private modalCtrl: ModalController,
    private plataform: Platform,
    private fundoService: FundoService
  ) {


  }

  async ngOnInit() {
    await this.plataform.ready()
      .then(() => {
        if (this.plataform.width() <= 700)
          this.fundoService.setCurrentPlataform('mobile');
        else
          this.fundoService.setCurrentPlataform('pc');

        this.img = this.fundoService.changeImg();
      });
  }

  ionViewWillEnter() {
  }

  nav() {
    this.router.navigateByUrl('novinimigo')
  }

  novo() {
    this.listDisplay = false
    if (this.display) {
      this.display = false;
    } else {
      this.display = true;
    }
  }

  getNotification(evt) {
    evt = Number.parseInt(evt);
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;

      this.consultar();
    }
  }

  consultar() {
    this.toast.clear();
    const p = this.npcService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  listar() {
    this.listDisplay = true;
    this.display = false;
  }

  destroyList() {
    this.listDisplay = false;
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  async selectedObj(event) {
    //console.log(event);

    this.npcService.setStaticID(event.id)
    const modal = await this.modalCtrl.create({
      component: UpdateNpcComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'princi'
    });
    modal.onDidDismiss()
      .then(res => {
        const { data } = res;
        if (data == true) {
          const p = this.npcService.findPageByCriador(this.paginaAtual.toString());
          this.paginaService.setPromisse(p);
          this.paginaService.requestRefresh(true);
        }
      });

    return await modal.present();
  }

  close() {
    this.listDisplay = false;
    this.display = false;
  }



}
